<template>
    <div class="quickviewWrapper">
        <div class="collapse-trigger" @click="toggle">
            <slot name="trigger" :open="isOpen" />
        </div>
        <div id="quickviewDefault" class="quickview" :class="{'is-active': isOpen}" v-show="isOpen">
            <header class="quickview-header">
                <p class="title">{{ title }}</p>
                <button
                    class="delete"
                    type="button"
                    @click="close"
                />
            </header>
            <div class="quickview-body" style="padding:0.75em">
                <div class="quickview-block">
                 <slot/>
                </div>
            </div>
            <footer v-if="footer" class="quickview-footer">

            </footer>
        </div>
    </div>
</template>

<script>
    export default {
        props: {
            open: {
                type: Boolean,
                default: true
            },
            animation: {
                type: String,
                default: 'fade'
            },
            ariaId: {
                type: String,
                default: ''
            },
            title: {
                type: String,
                default: 'Quickview'
            },
            footer: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {
                isOpen: this.open,
                items: {}
            }
        },
        watch: {
            open(value) {
                this.isOpen = value
            }
        },
        methods: {
            close() {
                this.isOpen = false;
            },
            toggle() {
                this.isOpen = !this.isOpen
                this.$emit('update:open', this.isOpen)
                this.$emit(this.isOpen ? 'open' : 'close')
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>

