<template>
    <span 
        class="button"
        :class="button"
        :disabled="items.buttonDisabled"
        @click="input()">
        <b-tag
            v-if="items.tagTitle"
            :ellipsis="items.ellipsis"
            :closable="items.tagClosable"
            :rounded="items.tagRounded"
            :type="items.tagColour"
            :size="items.tagSize">
            {{ items.tagTitle }}
        </b-tag>
        <b-icon 
            v-if="items.buttonIconLeft"
            :icon="items.buttonIconLeft"
            :size="items.buttonSize">
        </b-icon>
        <span>
            {{ items.buttonCount }} {{ items.buttonTitle }} {{ items.buttonSubtitle }}
        </span>
        <b-icon 
            v-if="items.buttonIconRight"
            :icon="items.buttonIconRight"
            :size="items.buttonSize">
        </b-icon>
    </span>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                items: {}
            }
        },
        computed: {
            button() {
                return [
                    this.items.buttonColour,
                    this.items.buttonSize,
                    this.items.buttonAlign,
                    this.items.buttonResponsive,
                    {'is-fullwidth': this.items.buttonExpanded},
                    {'is-static': this.items.buttonStatic},
                    {'is-outlined': this.items.buttonOutlined},
                    {'is-inverted': this.items.buttonInverted},
                    {'is-rounded': this.items.buttonRounded},
                    {'is-hovered': this.items.buttonHoverable},
                    {'is-focused': this.items.buttonFocusable},
                    {'is-active': this.items.buttonActive},
                    {'is-transparent': this.items.buttonTransparent},
                    {'is-loading': this.items.buttonLoading},
                    {'is-paddingless': this.items.buttonPaddingless},
                    {'has-font-bold': this.items.buttonBold}
                ];
            }
        },
        watch: {
            'opts': {
                handler() {
                    if (this.opts != null) {
                        this.items = Object.assign({}, this.opts);
                    }
                },
                deep: true
            }
        },
        methods: {
            input() {
                this.$emit('input', this.items.event);
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>

<style scoped>
span.button > .tag {
    margin-right: 1em;
}
.button.is-rounded {
    padding-left: 0.5em;
    padding-right: 0.5em;
}
.has-tag {
    margin-bottom: 2em;
}
.is-transparent {
    border-color: transparent;
    background-color: transparent;
}
.has-font-bold {
    font-weight: bold;
}
</style>
