<template>
    <b-table 
        :data="items.data" 
        :columns="items.columns"
        :bordered="items.bordered"
        :checked-rows.sync="items.value"
        :checkable="items.checkable"
        :striped="items.striped"
        :narrowed="items.narrowRows"
        :hoverable="items.hoverable"
        :loading="items.loading"
        :focusable="items.focusable"
        :mobile-cards="items.hasMobileCards"
        :default-sort="items.sortField"
        :default-sort-direction="items.defaultSortDirection"
        :paginated="items.isPaginated"
        :per-page="items.perPage"
        :pagination-simple="items.isPaginationSimple"
        @input="input">
        <template slot="empty">
            <section class="section">
                <div class="content has-text-grey has-text-centered">
                    <p>
                        <b-icon
                            icon="emoticon-sad"
                            size="is-large">
                        </b-icon>
                    </p>
                    <p>Nothing here.</p>
                </div>
            </section>
        </template>
        <!-- <template v-if="typeof tems.tableTooltips != 'undefined'" slot-scope="props" slot="header">
            <b-tooltip :active="!!props.column.meta" :label="props.column.meta" dashed>
                {{ props.column.label }}
            </b-tooltip>
        </template> -->
        <template v-if="items.total" slot="bottom-left">
            <b>Total checked</b>: {{ items.value.length }}
        </template> 
    </b-table>
</template>

<script>
    import debounce from 'lodash/debounce';
    export default {
        props: ['opts', 'field'],
        data() {
            return {
                items: {}
            }
        },
        methods: {
            input: debounce(function () {
                this.$emit('selection', this.field, this.items.value);
            }, 500)
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>