<template>
    <div :class="tabs">
        <div class="container" style="margin-bottom: 1em; padding-top: 1em;">
            <b-tabs 
                :expanded="items.tabExpanded"
                :type="items.tabSubtype"
                :size="items.tabSize"
                :position="items.tabPosition"
                v-model="selection"
                @input="input"
                @change="change">
                <b-tab-item 
                    v-for="(item, index) in items.tabs" 
                    :key="index"
                    :icon="item.icon"
                    :visible="item.tabVisible"
                    :label="item.tabTitle" 
                    :disabled="item.tabDisabled">
                    <template v-if="item.tabHeader" slot="header">
                        <b-icon :icon="item.tabIcon"></b-icon>
                        <span> {{ item.tabTitle }} <b-tag v-if="item.tabCount" rounded> {{ item.tabCount }} </b-tag> </span>
                    </template>
                </b-tab-item>
            </b-tabs>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['opts', 'parentOpts'],
        data() {
            return {
                items: {},
                selection: 0
            }
        },
        watch: {
            'opts': {
                handler() {
                    if (this.opts != null) {
                        this.items = Object.assign({}, this.opts);
                    }
                },
                deep: true
            }
        },
        methods: {
            input(value) {
                 this.$root.$emit('input', this.items.field, value)
            },
            change(value) {
                 this.$root.$emit('change', this.items.field, value)
            }
        },
        computed: {
            content() {
                return [
                    this.items.fontAlignment
                ]
            },
            tabs() {
                return [
                    this.items.tabsBackground
                ]
            }
        },
        beforeMount: function () {
            if (this.parentOpts != null) {
                this.items = Object.assign({}, this.parentOpts);
            } else if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>

<style>
.tabs a {
    margin-bottom: -3px !important;
}
.tabs li a span {
    display: inline-flex;
}
</style>
