<template>
    <b-field class="is-hidden-mobile" style="margin: 0.25em;">
        <p class="control"
            v-for="(item, index) in parentOpts.fieldControls" 
            :key="index">
            <s-component 
                :is="item.type" 
                v-show="item.show !== 'false'" 
                :opts="item"
                @input="input">
            </s-component>
        </p>
    </b-field>
</template>

<script>
    export default {
        props: ['opts', 'parentOpts'],
        data() {
            return {
                items: {}
            }
        },
        methods: {
            input() {
                this.$emit('input', this.items.event);
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>