import axios from 'axios';

const url = 'http://localhost:3000/api/paths/'

class PathsService {
    static getPaths() {
        return new Promise(async (resolve, reject) => {
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve(
                    data.map(path => ({
                        ...path,
                        createdAt: new Date(path.createdAt)
                    }))
                );
            } catch(err) {
                reject(err);
            }
        });
    }
    static updatePath(id, path) {
        return axios.patch(`${url}${id}`, path);
    }
    static insertPath(path) {
        return axios.post(url, {
            path
        });
    }
    static deletePath(id) {
        return axios.delete(`${url}${id}`);
    }
}

export default PathsService;
