<template>
    <b-field :label="items.title">
        <b-autocomplete
            :rounded="items.rounded"
            v-model="name"
            :data="filteredDataArray"
            :clear-on-select="items.clearOnSelect"
            :open-on-focus="items.openOnFocus"
            :keep-first="items.keepFirst"
            :field="items.field"
            :placeholder="items.subtitle"
            :size="items.size"
            :expanded="items.expanded"
            :icon-pack="items.iconpack"
            :icon="items.icon"
            :loading="items.loading"
            :maxlength="items.maxLength"
            @input="input"
            @select="selected">
            <template v-if="items.headerSlot" slot="header">
                <a @click="showAddOption">
                    <span>{{ items.headerSlot }}</span>
                </a> 
            </template>
            <template slot-scope="props">
                <div v-if="slotType == 'image'" class="media">
                    <div class="media-left">
                        <img width="32" :src="props.option">
                    </div>
                    <div class="media-content">
                        {{ props.option }}
                    </div>
                </div>
                <div v-else class="media">
                    {{ props.option }}
                </div>
            </template>
            <template v-if="items.emptySlot" slot="empty">{{ items.emptySlot }}</template>
        </b-autocomplete>
    </b-field>
</template>

<script>
    export default {
        props: ['opts', 'field', 'slotType'],
        data() {
            return {
                items: {},
                name: '',
                data: [
                    'Angular',
                    'Angular 2',
                    'Aurelia',
                    'Backbone',
                    'Ember',
                    'jQuery',
                    'Meteor',
                    'Node.js',
                    'Polymer',
                    'React',
                    'RxJS',
                    'Vue.js'
                ],  
            }
        },
        watch: {
            opts() {
                this.items = this.opts;
            }
        },
        computed: {
            filteredDataArray() {
                if (this.name == '') {
                    return;
                }
                return this.data.filter((option) => {
                    return option
                        .toString()
                        .toLowerCase()
                        .indexOf(this.name.toLowerCase()) >= 0
                })
            }
        },
        methods: {
            input() {
                this.$emit('input', this.items.field, this.filteredDataArray);
            },
            selected(option) {
                this.$emit('selected', this.items.field, option);
            },
            showAddOption() {
                this.$dialog.prompt({
                    message: `New Option`,
                    inputAttrs: {
                        placeholder: 'Please add a new option',
                        maxlength: 100,
                        value: this.name
                    },
                    confirmText: 'Add',
                    onConfirm: (value) => {
                        this.data.push(value)
                        this.$refs.autocomplete.setSelected(value)
                    }
                })
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
