<template>
    <div :class="items.fontAlignment">
        <span :class="items.dateSize" v-if="items.startDate">{{ startDate }}</span>
        <span v-if="items.startDate && items.startTime">&nbsp;</span>
        <span :class="items.timeSize" v-if="items.startTime">{{ startTime }}</span>
    </div>
</template>

<script>
    import moment from 'moment';
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        computed: {
            startDate() {
                var m = moment(this.items.startDate);
                if (this.items.fromDate) {
                    return m.fromNow();
                }
                if (this.items.calenderDate) {
                    return m.calendar();
                }
                if (this.items.shortDate) {
                    return m.format("DD-MM-YYYY");
                }
                return m.format("dddd, MMMM Do YYYY");
            },
            startTime() {
                var m = moment(this.items.startTime);
                if (this.items.amPm) {
                    return m.format("hh:mm A");
                }
                return m.format("HH:mm");
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
