<template>
    <b-field :label="items.title">
        <b-datepicker
            :size="items.size"
            :placeholder="items.subtitle"
            :icon="items.icon">
        </b-datepicker>
    </b-field>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
