<template>
    <div v-if="items.content" class="content" :class="content" v-html="decodeURIComponent(items.content)"></div>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        watch: {
            'opts': {
                handler() {
                    if (this.opts != null) {
                        this.items = Object.assign({}, this.opts);
                    }
                },
                deep: true
            }
        },
        computed: {
            content() {
                return [
                    this.items.fontAlignment,
                    this.items.fontWeight,
                    this.items.fontSize,
                    this.items.fontFamily,
                    {"is-italic": this.items.italic},
                    {"is-uppercase": this.items.uppercase},
                    {"is-lowercase": this.items.lowercase},
                    {"is-capitalized": this.items.capitalised},
                ]
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>
