<template>
    <div style="padding: 0.75rem" class="container">
        <button v-show="!(items.data && items.data.length)" class="button is-dark is-fullwidth" @click="fetchAllBlocks">
            <span>Fetch All Blocks</span>
        </button>
        <button v-show="items.data && items.data.length" class="button is-dark is-fullwidth" @click="resetBlocks">
            <span>Reset Blocks</span>
        </button>
        <s-divider v-show="!(items.data && items.data.length)"></s-divider>
        <b-field label="Search Blocks">
            <b-taginput
            v-model="items.data"
            :data="blocks"
            autocomplete
            field="name"
            icon="label"
            placeholder="Search for blocks to add to this path"
            @typing="getAsyncBlockData">
            </b-taginput>
        </b-field>
        <b-table 
            v-if="items.data && items.data.length"
            :paginated="isPaginated"
            :per-page="perPage"
            detailed
            checkable
            :checked-rows.sync="checkedRows"
            detail-key="_id"
            :data="items.data" 
            :columns="columns">
            <template slot="detail" slot-scope="props">
                <article class="media">
                    <div class="media-content" style="max-width: 75vw;">
                        <div class="content">
                            <s-code :opts="props.row"></s-code>
                        </div>
                    </div>
                </article>
            </template>
        </b-table>
         <b-notification :active.sync="changeBlocks">
            As we are in demo mode we won't make a request to the server.
        </b-notification>
        <button v-if="checkedRows.length" class="button is-danger is-fullwidth" @click="deleteSelection">
            <span>Delete Selected Blocks</span>
        </button>
    </div>
</template>

<script>
    const demoBlocks = require('./data/blocks_demo.json');
    import BlocksService from '../BlocksService'
    import debounce from 'lodash/debounce'
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                changeBlocks: false,
                checkedRows: [],
                demoBlocks,
                isPaginated: true,
                perPage: 5,
                filteredTags: [],
                blocks: [],
                items: {},
                columns: [
                    {
                        field: '_id',
                        label: 'ID',
                        width: '40'
                    },
                    {
                        field: 'name',
                        label: 'Name',
                        width: '40'
                    }
                 ]
            }
        },
        methods: {
            resetBlocks() {
                this.$set(this, 'items', {})
                this.$set(this, 'checkedRows', [])
            },
            async deleteSelection() {
                if (this.settings.demo) {
                    this.changeBlocks = true;
                    return;
                } else {
                    for (let row in this.checkedRows) {
                        await BlocksService.deletePath(this.checkedRows[row]._id);
                    }
                }
            },
            async fetchAllBlocks() {
                if (this.settings.demo) {
                    this.$set(this.items, 'data', demoBlocks)
                    return;
                }
                try {
                    let blocks = await BlocksService.getBlocks();
                    this.$set(this.items, 'data', blocks)
                } catch(err) {
                    this.error = err.message;
                }
            },
            getAsyncBlockData: debounce(function (text) {
                if (this.settings.demo) {
                    this.filteredTags = demoBlocks;
                    return;
                }
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/blocks?blocks=${text}`)
                    .then(({ data }) => {
                        this.blocks = [];
                        data.blocks.forEach((item) => this.blocks.push(item))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500)
        }
    }
</script>
