<template>
    <nav class="panel">
        <p class="panel-heading" :class="panelHeading">
            <s-level :opts="items" @input="input"></s-level>
            {{ items.panelTitle }}
        </p>
        <div
            v-for="(item, index) in items.panelItems" :key="index"
            class="panel-block" 
            :class="{'is-active': item.active}">
            <nav class="level">
                <div class="level-left">
                    <div class="level-item">
                        <b-icon 
                            :icon="item.panelIcon"
                            :size="item.panelIconSize">
                        </b-icon>
                    </div>
                    <div class="level-item">
                        <a href="#">{{ item.panelTitle }}</a>
                    </div>
                    <div class="level-item">
                        <p>{{ item.panelSubtitle }}</p>
                    </div>
                </div>
                <div class="level-right">
                    <div class="level-item">
                        <p>{{ item.panelDate }}</p>
                    </div>
                </div>
            </nav>
        </div>
    </nav>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        computed: {
            panelHeading() {
                return [
                    this.items.panelHeadingBackground,
                    {"panel-heading-rounded ": this.items.panelHeadingRounded}
                ]
            }
        },
        methods: {
            input(field, value) {
                this.$set(this.items, field, value);
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>

<style>
.panel-block {
    display: block !important;
}
.has-background-git {
    background-color: #f1f8ff !important;
    border: 1px solid #c8e1ff !important;

}
.panel-heading-rounded {
    border-radius: 4px 4px 4px 4px !important;
}
</style>

