<template>
    <div :class="levelWrapper">
        <nav class="level" :class="level">
            <div v-if="items.splitLevel" class="level-left">
                <div class="level-item" :class="items.levelFontAlignment">
                    <s-component
                        v-for="(item, index) in items.levelLeft" :key="index + 'levelLeft'"
                        :is="item.type"
                        :opts="item"
                        :parentOpts="items"
                        @input="input">
                    </s-component>
                </div>
            </div>
            <div v-if="items.splitLevel" class="level-right">
                <div class="level-item" :class="items.levelFontAlignment">
                    <s-component
                        v-for="(item, index) in items.levelRight" :key="index + 'levelRight'"
                        :is="item.type"
                        :opts="item"
                        :parentOpts="items"
                        @input="input">
                    </s-component>
                </div>
            </div>
            <div 
                v-else 
                v-for="(item, index) in items.levelMiddle" 
                :key="index + 'levelMiddle'"
                class="level-item" 
                :class="items.levelFontAlignment">
                <s-component
                    :is="item.type"
                    :opts="item"
                    @input="input">
                </s-component>
            </div>
        </nav>
    </div>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        methods: {
            input(field, value) {
                this.$emit('input', field, value);
            }
        },
        computed: {
            level() {
                return [
                    {'container': this.items.levelContainer},
                    {'is-mobile': this.items.levelMobile},
                    {'is-desktop': this.items.levelDesktop},
                    {'is-padded': this.items.levelPadded},
                    {'is-bordered': this.items.levelBordered}
                ]
            },
            levelWrapper() {
                return [
                    this.items.levelBackground
                ]
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>

<style scoped>
.is-padded {
    padding:0.75em;
}
.is-bordered {
    border: 1px solid #dfe2e5;
    border-radius: 3px;
    padding: 0.5em;
}
</style>
