<template>
    <div style="padding: 0.75rem" class="container">
        <b-field v-if="items._id" label="Page ID">
            <b-input v-model="items._id" disabled></b-input>
        </b-field>
        <b-field label="Add Page Title">
            <b-input maxlength="100" placeholder="Specify a page title" v-model="items.title" @input="update"></b-input>
        </b-field>
        <b-field label="Add Description">
            <b-input maxlength="173" type="textarea" placeholder="Provide a brief description of this page for search engines to display on their results page" v-model="items.description" @input="update"></b-input>
        </b-field>
        <b-field label="Add Path">
            <b-input maxlength="100" placeholder="Specify a path e.g. /about-us" v-model="items.name" @input="update" :disabled="items._id"></b-input>
        </b-field>
        <b-field label="Add Blocks">
            <b-taginput
                v-model="items.data"
                :data="blocks"
                autocomplete
                field="name"
                icon="label"
                placeholder="Search for blocks to add to this path"
                @typing="getAsyncBlockData">
            </b-taginput>
        </b-field>
        <b-field label="When the page features in a menu, should its children be visible on page load?">
            <b-switch v-model="items.display"></b-switch>
        </b-field>
        <b-field label="When the page features in a nav, should it dropdown to display its children">
            <b-switch v-model="items.dropdown"></b-switch>
        </b-field>
        <b-field label="Do you want this page to appear in site search listings?">
            <b-switch v-model="items.searchable"></b-switch>
        </b-field>
        <b-notification :active.sync="storeActive">
                As we are in demo mode we won't store the page.
        </b-notification>
        <b-field v-if="items._id">
            <button class="button is-success is-fullwidth" @click.prevent="updatePath">Update Page</button>
        </b-field>
        <b-field v-else>
            <button class="button is-success is-fullwidth" @click.prevent="createPath">Create Page</button>
        </b-field>
        <b-field label="Search for an existing page">
            <b-autocomplete
                class="is rounded"
                v-model="results"
                :data="pages"
                placeholder="e.g. about-us"
                field="name"
                :loading="isFetching"
                @input="getAsyncPageData"
                @select="populate">
            </b-autocomplete>
        </b-field>
        <input class="button is-danger" type="reset" @click="reset">
        <s-code :opts="items" :type="'s-page'"></s-code>
        <p class="control" v-if="items._id">
            <button class="button is-danger is-fullwidth" type="delete" @click="isComponentModalActive = true">Delete Page</button>
            <span class="help">Please copy the page id above before clicking the delete button.</span>
        </p>    
        <b-modal :width="300" :active.sync="isComponentModalActive">
            <section class="modal-card-body">
                <p class="subtitle">Are you sure you want to delete this page?</p>
                <b-field label="Please enter the page ID">
                    <b-input v-model="pageID"></b-input>
                </b-field>
                <p class="control" v-if="items._id">
                    <button class="button is-danger" type="delete" :disabled="!(pageID == items._id)" @click="deletePath">Delete Page</button>
                </p>
            </section>
        </b-modal>
        <b-modal :width="300" :active.sync="isNameModalActive">
            <section class="modal-card-body">
                <p class="subtitle">Your page <strong>must</strong> have a name. Please enter one.</p>
                <b-field label="Name">
                    <b-input v-model="items.name" required></b-input>
                </b-field>
                <p class="control">
                    <button class="button is-success" @click="createPath" :disabled="!items.name">Create path</button>
                </p>
            </section>
        </b-modal>
    </div>
</template>

<script>
    import PathsService from '../PathsService'
    import debounce from 'lodash/debounce'
    const demoPages = require('./data/pages_demo.json');
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                storeActive: false,
                demoPages,
                demoBlocks: ['homepage', 'about', 'contact', 'builder'],
                pageID: '',
                isComponentModalActive: false,
                isNameModalActive: false,
                results: '',
                pages: [],
                items: {},
                blocks: [],
                data: [],
                isFetching: false,
                error: ''
            }
        },
        methods: {
            populate() {
                this.items = this.pages[0];
                this.results = '';
            },
            async createPath() {
                if (this.items.name != null) {
                    if (this.settings.demo) {
                        this.storeActive = true;
                        this.isNameModalActive = false;
                        return;
                    }
                    await PathsService.insertPath(this.items);
                } else {
                    this.isNameModalActive = true;
                }
            },
            async updatePath() {
                if (this.settings.demo) {
                    this.storeActive = true;
                    return;
                }
                await PathsService.updatePath(this.items._id, this.items);
            },
            async deletePath() {
                if (this.pageID == this.items._id) {
                    if (this.settings.demo) {
                        this.storeActive = true;
                        this.isComponentModalActive = false;
                        return;
                    }
                    await PathsService.deletePath(this.items._id);
                    this.reset();
                    this.isComponentModalActive = false;
                }
            },
            reset() {
                if (this.opts != null) {
                    this.items = Object.assign({}, this.opts);
                }
            },
            update: debounce(function () {
                this.items = Object.assign({}, this.items);
            }, 200),
            getAsyncBlockData: debounce(function (text) {
                if (this.settings.demo) {
                    this.blocks = this.demoBlocks;
                    return;
                }
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/blocks?blocks=${text}`)
                    .then(({ data }) => {
                        this.filteredTags = [];
                        data.blocks.forEach((item) => this.blocks.push(item.name))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500),
            getAsyncPageData: debounce(function (text) {
                if (this.settings.demo) {
                    this.pages = demoPages;
                    return;
                }
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/paths?page=${text}`)
                    .then(({ data }) => {
                        this.pages = [];
                        data.paths.forEach((item) => this.pages.push(item))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500)
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>
