<template>
    <nav class="breadcrumb" :class="breadcrumbs" aria-label="breadcrumbs">
        <ul>
            <li 
                v-for="(item, index) in items.breadcrumbs" 
                :key="index"
                :class="item.active"
                >
                <a :href="item.link" :aria-current="item.aria">
                    {{ item.title }}
                </a>
            </li>
        </ul>
    </nav>
</template>

<script>
//What I want to happen here is to pass the current "crumb" in as a string and then using 
//the hierarchy defined in settings determine the path to this crumb.
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        computed: {
            breadcrumbs() {
                return [
                    this.items.colour || null
                ];
            },
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>