<template>
    <div class="container" style="margin-bottom:1em;">
        <s-component
                v-for="(item, index) in items['tab' + selection]" 
                :key="index + 'tab'"
                :is="item.type"
                :opts="item"
                @input="input">
        </s-component>
    </div>
</template>

<script>
    export default {
        props: ['opts', 'field'],
        data() {
            return {
                items: {},
                selection: 0
            }
        },
        watch: {
            'opts': {
                handler() {
                    if (this.opts != null) {
                        this.items = Object.assign({}, this.opts);
                    }
                },
                deep: true
            }
        },
        methods: {
            input(field, value) {
                this.$set(this.items, field, value);
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        },
        mounted() {
            this.$root.$on('input', (field, value) => {
                // eslint-disable-next-line
                console.log('Tab.vue input', field, value)
                this.$set(this, "selection", value);

            });
            this.$root.$on('change', (field, value) => {
                // eslint-disable-next-line
                console.log('Tab.vue change', field, value)
                this.$set(this, "selection", value);
            });
        }
    }
</script>

<style>
.tab-content {
    display:none !important;
}
</style>
