<template>
    <p class="delete" @click="input"></p>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        methods: {
            input() {
                this.$emit('input', this.items.field, false)
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
