<template>
    <b-field :grouped="items.grouped" :group-multiline="items.groupMultiline">
        <div class="control">
            <b-taglist v-if="items.blocks" :attached="items.attached">
                <b-tag 
                    v-for="(item, index) in items.blocks"
                    :key="index"
                    :type="item.tagColour" 
                    :size="item.tagSize">
                    {{ item.tagTitle }}
                </b-tag>
            </b-taglist>
            <b-tag 
                v-else
                :ellipsis="items.ellipsis"
                :closable="items.tagClosable"
                :rounded="items.rounded"
                :type="items.tagColour"
                :size="items.tagSize">
                {{ items.tagTitle }}
            </b-tag>
        </div>
    </b-field>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                items: {}
            }
        },
        computed: {
            fieldClasses() {
                return [
                    {'is-grouped': this.items.grouped},
                    {'has-addons': this.items.addons},
                    this.items.size

                ]
            }, 
            buttonsClasses() {
                return [
                    {'is-right': this.items.right},
                    {'has-addons': this.items.addons},
                    this.items.size

                ]
            },   
            buttonClasses() {
                return [
                    this.items.colour || (typeof this.settings != 'undefined' ? this.settings.colour : ''),
                    this.items.size || (typeof this.settings != 'undefined' ? this.settings.size : ''),
                    {'is-fullwidth': this.items.expanded},
                    {'is-static': this.items.static},
                    {'is-outlined': this.items.outlined},
                    {'is-inverted': this.items.inverted},
                    {'is-rounded': this.items.rounded},
                    {'is-hovered': this.items.hoverable},
                    {'is-focused': this.items.focusable},
                    {'is-active': this.items.active},
                    {'is-loading': this.items.loading}
                ];
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
