<template>
    <div v-if="items.headingLevel == 0" :class="items.fontAlignment">
        <p :class="title">
            {{ items.headingTitle }}
        </p>
    </div>
    <div v-else-if="items.headingLevel == 1" :class="items.fontAlignment">
        <h1
            class="title" 
            :class="title" 
            :id="items.anchor">
                <a v-if="items.anchor" :href="'#' + items.anchor" aria-hidden="true" class="header-title-anchor">#</a>
                {{ items.headingTitle }}
        </h1>
    </div>
    <div v-else :class="items.fontAlignment">
        <p
            class="title" 
            :class="title" 
            :id="items.anchor">
                <a v-if="items.anchor" :href="'#' + items.anchor" aria-hidden="true" class="header-title-anchor">#</a>
                {{ items.headingTitle }}
        </p>
    </div>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                items: {}
            }
        },
        computed: {
            title() {
                return [
                    {'is-underlined': this.items.headingUnderlined},
                    {'is-spaced': this.items.headingSpaced},
                    this.items.headingSize,
                    this.items.headingColour,
                    this.items.fontFamily
                ]
            }
        },
        watch: {
            'opts': {
                handler() {
                    if (this.opts != null) {
                        this.items = Object.assign({}, this.opts);
                    }
                },
                deep: true
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>

