<template>
    <footer class="footer">
        <s-columns v-if="items.blocks" :opts="items"></s-columns>
        <s-content :opts="items"></s-content>
        <!-- Place this tag where you want the button to render. -->
        <github-button href="https://github.com/buetify/buetify" data-size="large" data-show-count="true" aria-label="Watch ntkme/github-buttons on GitHub">Star</github-button>
    </footer>
</template>

<script>
    import GithubButton from 'vue-github-button'
    export default {
        components: {
            GithubButton
        },
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
