<template>
    <b-field
        v-if="items.blocks"
        :grouped="items.grouped"
        :label="items.title"
        :position="items.position">
        <b-switch 
            v-model="items.value"
            v-for="(item, index) in items.blocks" v-bind:key="index"
            :name="item.name"
            :size="item.size"
            :type="items.colour"
            :disabled="item.disabled"
            :native-value="item.nativeValue"
            :true-value="item.trueValue"
            :false-value="item.falseValue"
            @input="input">
                <span v-if="items.displayValue">
                    {{ items.value }}
                </span>
        </b-switch>
    </b-field>
    <b-field
        v-else
        :grouped="items.grouped"
        :label="items.title"
        :position="items.position">
        <b-switch 
            v-model="items.value"
            :name="items.name"
            :size="items.size"
            :type="items.colour"
            :disabled="items.disabled"
            :native-value="items.nativeValue"
            :true-value="items.trueValue"
            :false-value="items.falseValue"
            @input="input">
                <span v-if="items.displayValue">
                    {{ items.value }}
                </span>
        </b-switch>
    </b-field>
</template>

<script>
    export default {
        props: ['opts', 'field'],
        data() {
            return {
                items: {}
            }
        },
        methods: {
            input() {
                this.$emit('input', this.items.field, this.items.value)
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
            if (typeof this.field != 'undefined') {
                this.items.field = this.field;
            }
        }
    }
</script>
