<template>
    <div style="padding: 0.75rem" class="container">
            <b-field label="Taxonomy Name">
                <b-input maxlength="100" placeholder="Please specify a name for this taxonomy" v-model="items.name" @input="update"></b-input>
            </b-field>
            <b-field label="Menu/ Nav">
                <b-switch 
                    true-value="s-navbar"
                    false-value="s-menu"
                    v-model="component">
                </b-switch>
            </b-field>
            <s-navbar v-if="component == 's-navbar'" :opts="code.blocks" :settings="settings"></s-navbar>
            <s-menu v-else :opts="code" :settings="settings"></s-menu>
            <b-field label="Start typing the name of a page you want to add to your menu:">
                <b-taginput
                    v-model="items.blocks"
                    :data="filteredTags"
                    autocomplete
                    field="name"
                    icon="label"
                    placeholder="Search Pages"
                    @add="menuItemAdded"
                    @typing="getAsyncData">
                </b-taginput>
            </b-field>
            <s-taxonomy v-if="items.blocks" :opts="items.blocks" :settings="settings" :count="0"></s-taxonomy>
            <button v-if="!demo" class="button is-medium" @click="store">Save Taxonomy</button>
            <s-code :opts="code" :type="component"></s-code>
    </div>
</template>

<script>
    const pages = require('./data/pages_demo.json');
    import debounce from 'lodash/debounce'
    export default {
        props: ['taxonomy', 'opts', 'settings'],
        data() {
            return {
                currentItems: [],
                pages: [],
                component: 's-navbar',
                code: {},
                demo: true,
                path: '/admin/pages/fetch',
                items: {},
                filteredTags: [],
                filteredItems: [],
                isSelectOnly: false,
                allowNew: true
            }
        },
        watch: {
            items() {
                let temp = Object.assign({}, this.items);
                delete temp.demo;
                this.code = temp;
            }
        },
        methods: {
            menuItemAdded(item) {
                this.currentItems.push(item.name)
            },
            update: debounce(function () {
                this.items = Object.assign({}, this.items);
            }, 100),
            store () {
                this.$http.post(
                    '/api/paths', 
                    this.code,
                    {
                        headers: {
                            'Content-Type': 'application/json',
                        }
                    }
                ).then(response => {
                    this.$toast.open(response.data.response)
                }).catch((error) => {
                    throw error
                });
            },
            getFilteredTags(text) {
                this.filteredTags = this.pages.filter((option) => {
                    return option.name
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
            },
            getAsyncData : debounce(function (text) {
                if (this.settings.demo) {
                    return this.getFilteredTags(text)
                }
                this.data = []
                this.isFetching = true
                this.$http.get(`/api/paths?query=${text}`)
                    .then(({ data }) => {
                        this.filteredTags = [];
                        data.results.forEach((item) => this.filteredTags.push(item))
                        this.isFetching = false
                    })
                    .catch((error) => {
                        this.isFetching = false
                        throw error
                    })
            }, 500)
        },
        beforeMount() {
            if (this.opts !== null) {
                this.items = this.opts;
            }
            if (this.settings.demo) {
                this.pages = pages;
            }
        }
    }
</script>
