<template>
    <figure class="image" :class="figure">
        <img ref="img" :class="image" :src="items.imagePath" :alt="items.imageAlt" :style="{'height' : items.height, 'width' : items.width}">
        <figcaption v-if="items.figCaption">{{items.figCaption}}</figcaption>
    </figure>
</template>

<script>
    export default {
        props: ['opts', 'demo'],
        data() {
            return {
                items: {},
                height: null,
                width: null
            }
        },
        computed: {
            figure() {
                return [
                    this.items.figureSize
                ];
            },
            image() {
                return [
                    this.items.figureRounded 
                ];
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        },
        updated: function () {
            if (this.demo && typeof this.items.height == 'undefined' && typeof this.items.width == 'undefined') {
                this.$set(this.items, 'height', this.$refs.img.naturalHeight + 'px')
                this.$set(this.items, 'width', this.$refs.img.naturalWidth + 'px')
            }
            if (this.demo && this.items.height == '0' && this.items.width == '0') {
                this.$set(this.items, 'height', null)
                this.$set(this.items, 'width', null)
            }
        }
    }
</script>

<style>
.is-rounded-shallow {
    border-radius: 2px;
}
</style>
