<template>
    <aside ref="menu" class="menu" :class="[menu, {'fixed': fixed}]" style="padding:0.75em">
        <div v-for="(item, key) in items.blocks" :key="key" :index="key">
            <p v-if="item.category" class="menu-label">
                {{ item.category }}
            </p>
            <b-collapse :open="open">
                <ul class="menu-list">
                    <s-menuitem :item="item"></s-menuitem>
                </ul>
            </b-collapse>
        </div>
    </aside>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                open: true,
                items: [],
                viewportOffsetTop: 0,
                fixed: false
            }
        },
        watch: {
            opts() {
                if (typeof this.items.blocks == 'undefined' && this.settings.menu != null) {
                    this.$set(this, 'items', this.settings.menu);
                } else {
                    this.$set(this, 'items', this.opts);
                }
            }
        },
        computed: {
            menu() {
                return [
                    this.items.visible || null
                ];
            }
        },
        methods: {
            handleScroll () {
                if(window.scrollY > this.viewportOffsetTop) {
                    this.fixed = true;
                } else {
                    this.fixed = false;
                }
            }
        },
        created () {
            window.addEventListener('scroll', this.handleScroll);
        },
        destroyed () {
            window.removeEventListener('scroll', this.handleScroll);
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            } 
            if (typeof this.items.blocks == 'undefined' && this.settings.menu != null) {
                this.items = this.settings.menu;
            }
        },
        mounted() {
            let viewportOffset = this.$refs.menu.getBoundingClientRect();
            this.viewportOffsetTop = viewportOffset.top;
        }
    }
</script>

<style scoped>
.fixed {
  position: sticky !important;
  top: 0;
}
</style>


