<template>
        <hr v-if="item.divider" class="navbar-divider">
        <a v-else-if="!item.dropdown" :href="item.link" class="navbar-item">
            {{ item.title }}
        </a>
        <div v-else class="navbar-item" :class="navClasses">
            <a v-if="item.title" :href="item.link" class="navbar-link" :class="{'is-arrowless': settings.navArrowless, 'arrow-up': show}" @click="show = !show;">
                {{ item.title }}
            </a>
            <div v-if="item.blocks" class="navbar-dropdown" :class="{'is-right': item.isRight, 'show': show, 'hide': !show}">
                <s-navitem :item="item" v-for="(item, key) in item.blocks" :key="key" :index="key" :settings="settings"></s-navitem>
            </div>
        </div>
</template>

<script>
    export default {
        props: ['item', 'settings'],
        data() {
            return {
                show: false
            }
        },
        computed: {
            navClasses() {
                return [
                    {'hidden': this.item.hidden ? this.item.hidden : false},
                    {'has-dropdown': this.item.dropdown},
                    {'has-dropdown-up': this.item.dropdownUp}, 
                    {'is-hoverable': this.settings.navHoverable}
                ];
            }
        }
    }
</script>

<style>
.navbar-link:not(.is-hoverable).arrow-up::after {
    -webkit-transform: rotate(-225deg) !important;
    transform: rotate(-225deg) !important;
}
.show {
    display: block;
}
.hide {
    display: none;
}
</style>
