<template>
    <header style="position:relative;overflow:hidden;">
        <section v-if="items.displayHero" class="hero" :class="hero">
            <div v-if="items.displayHeroNavbar" class="hero-head">
                <s-navbar :settings="settings"></s-navbar>
            </div>
            <div class="hero-head">
                <s-navbar v-if="items.heroNav" :settings="settings"></s-navbar>
                <s-level v-if="items.heroLevel" :opts="items" @input="input"></s-level>
            </div>
            <div v-if="(typeof this.items.skew == 'undefined' ? true : false)" class="hero-body" :class="heroBody">
                <div :class="heroMiddle">
                    <s-component
                        v-for="(item, index) in items.heroContent" :key="index + 'heroContent'"
                        :is="item.type"
                        :opts="item">
                    </s-component>
                </div>
                <div class="columns" style="margin-top: 1em;width:100%;">
                     <div class="column is-half">
                        <div>
                            <s-component
                                style="margin-bottom: 1em;"
                                v-for="(item, index) in items.heroContentLeft" :key="index + 'heroContentLeft'"
                                :is="item.type"
                                :opts="item"
                                :secondaryOpts="items">
                            </s-component>
                        </div>
                        <div class="buttons">
                            <s-component
                                v-for="(item, index) in items.heroButtonsLeftBottom" :key="index + 'heroButtonsLeftBottom'"
                                :is="item.type"
                                :opts="item">
                            </s-component>
                        </div>
                     </div>
                    <div class="column is-half">
                        <s-component
                                v-for="(item, index) in items.heroContentRight" :key="index + 'heroContentRight'"
                                :is="item.type"
                                :opts="item"
                                :secondaryOpts="items">
                        </s-component>
                    </div>
                </div>
            </div>
            <div v-if="false" class="hero-foot">
                <s-tab :opts="items"></s-tab>
            </div>
        </section>
        <section v-if="items.skew && items.data">
            <div class="hero-body" :class="heroBody">
                <s-button :opts="items"></s-button>
                <s-heading :opts="items"></s-heading>
                <div class="buttons">
                    <div v-for="(item, index) in items.data" v-bind:key="index">
                        <s-button :opts="item"></s-button>
                    </div>
                </div>
            </div>
        </section>
        <div v-if="items.heroNotifications">
            <s-component
                v-for="(item, index) in items.heroNotifications" :key="index + 'heroNotifications'"
                :is="item.type"
                :opts="item">
            </s-component>
        </div>
    </header>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                buttons: ['s-button'],
                items: {},
                visible: true
            }
        },
        watch: {
            'opts': {
                handler() {
                    if (this.opts != null) {
                        this.items = Object.assign({}, this.opts);
                    }
                },
                deep: true
            }
        },
        methods: {
            input(field, value) {
                this.$set(this.items, field, value);
            }
        },
        computed: {
            hero() {
                return [
                    {'is-bold': this.items.heroBold},
                    this.items.heroColour,
                    this.items.heroSize,
                    {'is-skewed': this.items.heroSkew},
                    this.items.heroSkewType || ''
                ];
            },
            heroBody() {
                return [
                    {'is-paddingless': this.items.heroPaddingless},
                    {'skew-content': this.items.heroSkew}
                ];
            },
            heroMiddle() {
                return [
                    this.items.heroMiddleAlignment
                ];
            }
        },
        mounted() {
            if (this.items.launchSnackbar) {
                this.$snackbar.open({
                    message: 'Terms, Privacy, Currency & More',
                    type: 'is-danger',
                    queue: false,
                    position: 'is-bottom-right',
                    actionText: 'Close',
                    indefinite: true
                })
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>

<style>
.skew-content {
    position: relative;
    height: calc(130px * 4);
}
.is-skewed {
    width: 100%;
    height: 100%;
    overflow: hidden;
    position: absolute;
}
.skew-left {
    -webkit-transform: skewY(-12deg) translate(0px,-112px);
    transform: skewY(-12deg) translate(0px,-112px);
    -webkit-transform-origin: 0;
    transform-origin: 0;
}
.skew-right {
    -webkit-transform: skewY(12deg) translate(0px,-413px);
    transform: skewY(12deg) translate(0px,-413px);
    -webkit-transform-origin: 0;
    transform-origin: 0;
}
.notices .snackbar {
    background: #ffffff !important;
    color: #151515 !important;
}
.notices .snackbar .button {
    background: #ffffff !important;
    color: #151515 !important;
}
</style>
