<template>
    <b-field
        v-if="items.blocks"
        :grouped="items.grouped"
        :label="items.title"
        :position="items.position">
        <b-radio
            v-for="(item, index) in items.blocks" v-bind:key="index"
            v-model="items.value"
            :native-value="item.nativeValue"
            :name="item.name"
            :size="item.size"
            :disabled="item.disabled"
            :type="item.colour"
            @input="input">
            <span v-if="items.displayValue">
                {{ item.subtitle }}
            </span>
        </b-radio>
    </b-field>
    <b-field
        v-else
        :grouped="items.grouped"
        :label="items.title"
        :type="items.colour"
        :position="items.position"
        :message="items.description">
        <b-radio
            v-model="items.value"
            :native-value="items.nativeValue"
            :name="items.name"
            :size="items.size"
            :disabled="items.disabled"
            :type="items.colour"
            @input="input">
            <span v-if="items.displayValue">
                {{ items.subtitle }}
            </span>
        </b-radio>
    </b-field>
</template>

<script>
    export default {
        props: ['opts', 'field'],
        data() {
            return {
                items: {}
            }
        },
        methods: {
            input() {
                this.$emit('radioSelection', this.field, this.items.value)
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
