<template>
    <div :class="items.authorSize" v-if="items.author">{{ items.author }} <a class="button is-small is-outlined is-info">Follow</a></div>
</template>

<script>
    export default {
        props: ['opts'],
        data() {
            return {
                items: {}
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>