                ],
                copying: false,
                optionsFields: [
                    "navHoverable",
                    "navbarEnd",
                    "navFixedTop",
                    "navFixedBottom",
                    "navArrowless"
                ],
                classFields: [
                    "navColour"
                ]
            }
        },
        computed: {
            filteredDataArray() {
                return this.data.filter((option) => {
                    // eslint-disable-next-line
                    return option
                        .toString()
                        .toLowerCase()
                        .indexOf(this.name.toLowerCase()) >= 0
                })
            }
        },
        methods: {
            update: debounce(function () {
                this.settings = Object.assign({}, this.settings);
            }, 100),
            copy() {
                this.copying = true;
                var textArea = document.createElement("textarea");
                textArea.value = JSON.stringify(this.settings);
                document.getElementById("copy").appendChild(textArea);
                textArea.focus();
                textArea.select();
                try {
                    document.execCommand('copy');
                    setTimeout(() => {this.copying = false;}, 500);
                } catch (err) {
                    // eslint-disable-next-line
                    console.log(err);
                }
                document.getElementById("copy").removeChild(textArea);
            },
            typing(text) {
                this.name = text;
            }
        }
    }
