<template>
    <div>
        <div class="columns" :class="columns">
            <div class="column is-3" :class="[items.columnOffset, {'is-narrow': items.narrow}, items.visible]">
                <s-menu v-if="items.menuBlocks" :opts="items.menuBlocks[0]" :settings="settings"></s-menu>
                <s-menu v-else :opts="null" :settings="settings"></s-menu>
            </div>
            <div class="column" style="padding-left: 2rem;padding-right: 2rem;" :class="[items.columnSize, items.columnOffset, {'is-narrow': items.narrow}, items.visible]">
                <div v-for="(item, index) in items.data" v-bind:key="index">
                    <s-heading class="headingSpace" :opts="item"></s-heading>
                    <s-content class="contentSpace" :opts="item" ></s-content>
                    <div v-for="(block, index) in item.blocks" v-bind:key="index">
                        <s-codesnippet v-if="block.type == 's-code'" :opts="block"></s-codesnippet>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                items: {}
            }
        },
        computed: {
            columns() {
                return [
                    {'is-mobile': this.items.mobile},
                    {'is-desktop': this.items.desktop},
                    {'is-multiline': this.items.multiline}, 
                    {'is-gapless': this.items.gapless || (typeof this.settings != 'undefined' ? this.settings.gapless : false)}, 
                    {'is-centered': this.items.centred}, 
                    {'is-vcentered': this.items.verticallyCentred}, 
                    {'is-variable': this.items.variableGap || (typeof this.settings != 'undefined' ? this.settings.variableGap : false)},
                    this.items.visible,
                    this.items.mobileGapSize || (typeof this.settings != 'undefined' ? this.settings.mobileGapSize : ''),
                    this.items.tabletGapSize || (typeof this.settings != 'undefined' ? this.settings.tabletGapSize : ''),
                    this.items.desktopGapSize || (typeof this.settings != 'undefined' ? this.settings.desktopGapSize : ''),
                    this.items.widescreenGapSize || (typeof this.settings != 'undefined' ? this.settings.widescreenGapSize : ''),
                    this.items.fullhdGapSize || (typeof this.settings != 'undefined' ? this.settings.fulldhGapSize : '')
                ];
            }
        },
        beforeMount: function () {
            if (this.opts !== null) {
                this.items = this.opts;
            }
        }
    }
</script>

<style scoped>
.contentSpace {
    margin-block-start: 1em;
    margin-block-end: 1em;
}
</style>
