<template>
<div>
    <section class="hero is-medium">
        <div class="hero-body">
            <div class="container has-text-centered">
                <h1 class="title is-size-1-mobile racing">
                    Buetify<sup>&reg;</sup>
                </h1>
                <h2 class="subtitle is-4">
                    The dynamic website generator.
                </h2>
            </div>
        </div>
    </section>
    <div class="section container has-text-centered">
        <div class="buttons are-large is-centered">
            <a class="button" href="https://github.com/buetify/buetify/archive/master.zip">
                <b-icon icon="github-circle"></b-icon>
                <span>Download</span>
            </a>
            <a class="button" href="/guide">
                <b-icon icon="book-open-variant"></b-icon>
                <span>Guide</span>
            </a>
        </div>
    </div>
    <div class="section container" style="height: 100vh">
        <transition name="fade">
            <s-component v-if="display" :data="items" :settings="settings"></s-component>
        </transition>
    </div>
</div>
</template>
<script>
    const demoBlocks = require('./data/component_demo.json');
    export default {
        props: ['opts', 'settings'],
        data() {
            return {
                display: false,
                blocks: [],
                items: [],
                itemsLength: 0,
                demoBlocks,
                count: 0
            }
        },
        methods: {
            toggle() {
                this.blocks = demoBlocks
                this.itemsLength = this.blocks.length;
            },
            delay(ms) {
                return new Promise(res => setTimeout(res, ms))
            }
        },
        beforeMount() {
            this.toggle();
            setInterval(async () => {
                this.display = false;
                await this.delay(3000);
                this.items = [];
                this.items.push(this.blocks[this.count % this.itemsLength]);
                this.items.push();
                this.count += 1;
                this.display = true;
            }, 5000);

        }
    }
</script>

