<template>
    <b-field :class="dropdown" style="margin-bottom: 0px; margin: 0.25em;">
        <p class="control">
            <b-dropdown
                :position="items.dropdownPosition">
                <button 
                    class="button" 
                    :class="button"
                    slot="trigger">
                    <b-icon 
                        v-if="items.buttonIconLeft"
                        :icon="items.buttonIconLeft"
                        :size="items.buttonSize">
                    </b-icon>
                    <span>
                    {{ items.buttonCount }} {{ items.buttonTitle }} {{ items.buttonSubtitle }}
                    </span>
                    <b-icon 
                        v-if="items.buttonIconRight"
                        :icon="items.buttonIconRight"
                        :size="items.buttonSize">
                    </b-icon>
                </button>
                <b-dropdown-item 
                    v-if="items.dropdownTitle"
                    :custom="items.dropdownCustom"
                    :paddingless="items.dropdownPaddingless">
                    <div v-if="items.dropdownModalCard" class="modal-card" style="width:300px;">
                        <header class="modal-card-head">
                            {{ items.dropdownTitle }}
                        </header>
                        <section class="modal-card-body">
                            <s-component
                                :is="items.subtype"
                                :opts="parentOpts"
                                @input="input">
                            </s-component>
                        </section>
                        <footer class="modal-card-foot">
                            <ul>
                                <li 
                                    v-for="(item, index) in data"
                                    :key="index"
                                    class="media">
                                    {{ item }}
                                </li>
                            </ul>
                        </footer>
                    </div>
                    <div v-else-if="items.dropdownCard" class="card" style="width:300px;">
                        <header class="card-header">
                            <p class="card-header-title">
                                {{ items.dropdownTitle }}
                            </p>
                        </header>
                        <div class="card-content">
                             {{ items.dropdownSubtitle }}
                            <s-component
                                :is="items.subtype"
                                :opts="parentOpts"
                                @input="input">
                            </s-component>
                        </div>
                        <footer class="card-footer">
                            <p class="card-footer-item">
                                <a href="#">
                                    Open in Desktop
                                </a>
                            </p>
                            <p class="card-footer-item">
                                <a href="#">
                                    Download ZIP
                                </a>
                            </p>
                        </footer>
                    </div>
                    <span v-else>{{ items.dropdownTitle }}</span>
                </b-dropdown-item>
            </b-dropdown>
        </p>
        <p v-if="items.dropdownCount" class="control">
            <button class="button is-primary">
                {{ items.dropdownCount }}
            </button>
        </p>
    </b-field>
</template>

<script>
    export default {
        props: ['opts', 'parentOpts'],
        data() {
            return {
                items: {},
                data: null
            }
        },
        computed: {
            dropdown() {
                return [
                    this.items.colour || null
                ];
            },
            button() {
                return [
                    this.items.buttonColour || null
                ];
            },
        },
        methods: {
            input(field, value) {
                this.data = value;
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>

<style>
.dropdown-content {
    padding-bottom:0px !important;
    padding-top:0px !important;
}
.modal-card-body div.dropdown-menu {
    display: none !important;
}
</style>

