        computed: {
            filteredDataArray() {
                return this.data.filter((option) => {
                    return option
                        .toString()
                        .toLowerCase()
                        .indexOf(this.name.toLowerCase()) >= 0
                })
            },
            filteredDataObj() {
                return this.demos.filter((option) => {
                    return option
                        .toString()
                        .toLowerCase()
                        .indexOf(this.name.toLowerCase()) >= 0
                })
            }
        },
        watch: {
            content() {
                this.$set(this.items, 'content', this.content);
            }
        },
        methods: {
            async fetchFiles () {
                let albumBucketName = 'stellifysoftware';
                let bucketRegion = 'eu-west-2';
                let bucketPath = 'https://s3.eu-west-2.amazonaws.com/stellifysoftware/';
                let IdentityPoolId = 'eu-west-2:a4f5c7d6-76e9-4406-a9dc-04b4a76f263c';

                AWS.config.update({
                    region: bucketRegion,
                    credentials: new AWS.CognitoIdentityCredentials({
                    IdentityPoolId: IdentityPoolId
                    })
                });

                var s3 = new AWS.S3({
                    apiVersion: '2006-03-01',
                    params: {Bucket: albumBucketName}
                });

                await s3.listObjects({Delimiter: '/'}, function(err, data) {
                    if (err) {
                        return alert('There was an error listing your albums: ' + err.message);
                    } else {
                        this.files = data.Contents.map(function(file) {
                            return bucketPath + file.Key
                        });
                    }
                }.bind(this));
            },
            handlerFunction: debounce(function () {
                this.$set(this.items, 'content', this.content);
            }, 200),
            timeNow(timeField) {
                this.$set(this.items, timeField, new Date());
            },
            timeClear(timeField) {
                this.items[timeField] = null;
            },
            getFilteredBlocks(text) {
                this.filteredBlocks = blocks.filter((option) => {
                    return option.name
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
            },
            getFilteredSecondaryBlocks(text) {
                this.filteredSecondaryBlocks = this.blocks.filter((option) => {
                    return option.name
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
            },
            getFilteredColumns(text) {
                this.filteredColumns = this.columns.filter((option) => {
                    return option.name
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
            },
            getFilteredData(text) {
                this.filteredData = this.data.filter((option) => {
                    return option.name
                        .toString()
                        .toLowerCase()
                        .indexOf(text.toLowerCase()) >= 0
                })
            },
            reset() {
                if (this.opts != null) {
                    this.items = Object.assign({}, this.opts);
                }
            },
            populate(field, text) {
                this.$set(this.items, field, text);
                this.$set(this.code, field, text);
            },
            async storeBlock() {
                if (this.settings.demo == true) {
                    this.storeActive = !this.storeActive;
                } else {
                    if (this.items.name != null) {
                        var items = Object.assign({}, this.items);
                        if (this.items.data) {
                            let data = items.data.map(function(data) {
                                return data.name;
                            });
                            items.data = data;
                        }
                        await BlocksService.insertBlock(items);
                        this.$toast.open('Block Created!')
                        this.isComponentModalActive = false;
                    } else {
                        this.isComponentModalActive = true;
                    }
                }
            },
            addDemo(selection) {
                if (typeof selection.content != 'undefined') {
                    this.content = decodeURIComponent(selection.content);
                }
                selection.block = {};
                selection.block.type = selection.type;
                selection.type = 's-builder';
                this.$set(this, 'items', selection);
            }
        },
        beforeMount: async function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
            if (this.settings.demo) {
                this.blocks = blocks;
                this.data = data;
                this.columns = columns;
            }
