<template>
    <b-field
        :grouped="items.grouped"
        :label="items.title"
        :type="items.colour"
        :position="items.position"
        :message="items.description">
        <b-select
            v-model="items.value"
            :name="items.name"
            :expanded="items.expanded"
            :size="items.size"
            :native-size="items.nativeSize"
            :placeholder="items.subtitle"
            :icon-pack="items.iconpack"
            :icon="items.icon"
            :loading="items.loading"
            :required="items.required"
            :disabled="items.disabled"
            :rounded="items.rounded"
            :multiple="items.multiple"
            @input="input">
            <option
                v-for="(item, key) in items.blocks" 
                :index="key"
                :key="item.id"
                :value="item.id"
                :disabled="item.disabled">
                {{ item.title }}
            </option>
        </b-select>
    </b-field>
</template>

<script>
    import debounce from 'lodash/debounce';
    export default {
        props: ['opts', 'field', 'settings'],
        data() {
            return {
                items: {},
                type: 'range'
            }
        },
        methods: {
            input: debounce(function (text) {
                if (this.items.subtype == 'range') {
                    this.items.description = text;
                }
                this.$emit('selection', this.field, this.items.value);
            }, 500)
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
            }
        }
    }
</script>
