<template>
    <form :class="form">
        <div v-for="(item, index) in items.formControls" :key="index" style="margin-bottom:1em;"> 
            <s-component 
                :is="item.type" 
                v-show="item.show !== 'false'" 
                :opts="item"
                @input="input"
                @focus="focus"
                @blur="blur"
            ></s-component>
        </div>
    </form>
</template>

<script>
    export default {
        props: ['opts','secondaryOpts'],
        data() {
            return {
                formComponents: ['s-input', 's-switch'],
                items: {}
            }
        },
        computed: {
            form() {
                return [
                    this.items.formWidth,
                    this.items.formAlignment,
                    {'margin-center': this.items.formCentered},
                    {'box': this.items.formBoxed}
                ];
            }
        },
        watch: {
            'secondaryOpts': {
                handler() {
                    if (this.secondaryOpts != null) {
                        this.items = Object.assign({}, this.secondaryOpts);
                    } else if (this.opts != null) {
                        this.items = Object.assign({}, this.opts);
                    }
                },
                deep: true
            }
        },
        methods: {
            input(field, value) {
                // eslint-disable-next-line
                console.log('input', field, value);
                this.$emit('input', field, value);
            },  
            focus(field, value) {
                // eslint-disable-next-line
                console.log('focus', field, value);
            },
            blur(field, value) {
                // eslint-disable-next-line
                console.log('blur', field, value);
            }
        },
        beforeMount: function () {
            if (this.secondaryOpts != null) {
                this.items = Object.assign({}, this.secondaryOpts);
            } else if (this.opts != null) {
                this.items = Object.assign({}, this.opts);
            }
        }
    }
</script>

<style scoped>
.margin-center {
    margin: auto;
}
.mw25 {
    max-width: 25em;
}
</style>
