<template>
    <b-field
        v-if="items.blocks"
        :grouped="items.grouped"
        :label="items.title"
        :type="items.colour"
        :position="items.position">
        <b-checkbox
            v-for="(item, index) in items.checkboxBlocks" v-bind:key="index"
            v-model="selections"
            :native-value="item.nativeValue"
            :name="item.name"
            :true-value="item.trueValue"
            :false-value="item.falseValue"
            :size="item.size"
            :disabled="item.disabled"
            :type="item.colour"
            @input="inputGroup">
        </b-checkbox>
        <span v-if="items.displayValue">{{ selections }}</span>
    </b-field>
    <b-field
        v-else
        :grouped="items.grouped"
        :label="items.title"
        :type="items.colour"
        :position="items.position">
        <b-checkbox
            v-model="selection"
            :native-value="items.nativeValue"
            :name="items.name"
            :true-value="items.trueValue"
            :false-value="items.falseValue"
            :size="items.size"
            :disabled="items.disabled"
            :type="items.colour"
            @input="input">
            <span v-if="items.displayValue">{{ selection }}</span>
        </b-checkbox>
    </b-field>
</template>

<script>
    export default {
        props: ['opts', 'field'],
        data() {
            return {
                selections: [],
                selection: '',
                items: {}
            }
        },
        methods: {
            inputGroup() {
                this.$emit('selection', 'value', this.selections)
            },
            input() {
                this.$emit('selection', 'value', this.selection)
            }
        },
        beforeMount: function () {
            if (this.opts != null) {
                this.items = this.opts;
                this.items.value = []
            }
        }
    }
</script>
