<template>
    <div>
        <div id="copy"></div>
        <div class="code-wrapper">
            <span class="click-to-copy" :class="{'copying': copying}" @click="copy">Copy</span>
            <div class="language" :class="language">
                <pre class="code" v-html="items"></pre>
            </div>
        </div>
    </div>
</template>

<script>
  export default {
    props: ['opts'],
    data() {
      return {
          items: {},
          copying: false,
          language: 'json'
      }
    },
    watch: {
        'opts': {
            handler() {
                if (this.opts != null) {
                    this.items = Object.assign({}, this.opts);
                    if (typeof this.items.content !== 'undefined') {
                        this.items.content = encodeURIComponent(String(this.items.content))
                    }
                    if (typeof this.items.block != "undefined") {
                        this.$set(this.items, 'type', this.items.block.type);
                    }
                    delete this.items.block;
                }
            },
            deep: true
        }
    },
    methods: {
        copy() {
            var textArea = document.createElement("textarea");
            textArea.value = JSON.stringify(this.items);
            document.getElementById("copy").appendChild(textArea);
            textArea.focus();
            textArea.select();
            try {
                document.execCommand('copy');
                this.$toast.open('Copied!')
            } catch (err) {
                this.$toast.open(err)
            }
            document.getElementById("copy").removeChild(textArea);
        }
    },
    beforeMount: function () {
        if (this.opts != null) {
            this.items = Object.assign({}, this.opts);
            if (typeof this.items.block != "undefined") {
                this.items.type = this.items.block.type;
                delete this.items.block;
                if (this.items.language) {
                    this.language = this.items.language;
                    delete this.items.language;
                }
            }
        }
    }
}
</script>

<style scoped>
    pre {
        line-height: 1.4;
        padding: 1.25rem 1.5rem;
        margin: .85rem 0;
        background-color: #282c34;
        border-radius: 6px;
        overflow: auto;
        color: white;
        white-space: pre-wrap;
    }
    .language {
        position: relative;
    }
    .json:before {
        content: "JSON";
    }
    .environment:before {
        content: "env";
    }
    .shell:before {
        content: "SH";
    }
    .language:before {
        position: absolute;
        z-index: 3;
        top: .8em;
        right: 1em;
        font-size: .75rem;
        color: hsla(0,0%,100%,.4);
    }
    .code-wrapper {
        margin-top: 0.75rem;
        display: block;
    }
    .code-wrapper:hover .click-to-copy {
        display: block;
    }
    .click-to-copy {
        display: none;
    }
    .click-to-copy {
        color: #0069ff;
        font-size: 13px;
        line-height: 100%;
        position: relative;
        opacity: .999;
        transition: opacity .2s ease-in-out;
        top: -1px;
    }
    .click-to-copy {
        cursor: pointer;
    }
</style>
