<template>
    <div>
        <component
            :settings="settings"
            v-for="(item, key) in items"
            :key="key"
            :index="key"
            :is="item.type"
            :opts="item">
        </component>
    </div>
</template>

<script>
export default {
    props: ['data', 'settings'],
    data() {
        return {
            items: null
        };
    },
    watch: {
        'data': {
            handler() {
                if (this.data != null) {
                    this.items = Object.assign({}, this.data);
                }
            },
            deep: true
        }
    },
    beforeMount: function () {
        if (this.data != null) {
            this.items = Object.assign({}, this.data);
        }
    }
}
</script>
